"use client";
import React, { useState } from 'react';
import Header from '@/components/Header';

interface ExperienceData {
    name: string;
    description: string[];
    date: string;
}

const Experience: React.FC = () => {
    const [hoveredIndex, setHoveredIndex] = useState<number | null>(null);

    return (
        <div className=' md:px-0' >
            <Header title="Previous" titleClass='text-center text-4xl md:text-6xl lg:text-left' title2="Experiences" />

            <div className=" my-10 flex font-outfit px-5 lg:px-0 flex-col gap-3">
                {data.map((item, index) => (
                    <div 
                        key={index} 
                        onMouseEnter={() => setHoveredIndex(index)}
                        onMouseLeave={() => setHoveredIndex(null)} 
                        className={`p-5 rounded-xl flex hoveredIndex ${hoveredIndex === index ? 'text-white bg-primary duration-200 delay-75' : 'bg-zinc-800'}`}>
                        <div className=''>
                            <h1 className={`text-white text-2xl font-semibold `}>{item.name}</h1>
                            {item.description.map((desc, descIndex) => (
                                <p className='text-white py-2 font-medium text-base ' key={descIndex}>{desc}</p>
                            ))}
                            <h1 className='text-white text-base'>{item.date}</h1>
                        </div>
                        <div className={`${hoveredIndex === index ? "p-0 duration-200" : "pt-2 pr-2"}`}>
                            {icon}
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
}

export default Experience;

