"use client"
import Link from 'next/link'
import React, { useState } from 'react'

const SocialMedia = () => {
    const [hoverIndex, setHoverIndex] = useState<number | null>(null)
    const links = [
        {
            name: "github",
            link: "https://github.com/Dineshyadav3505",
            image: github
        },
        {
            name: "linkedin",
            link: "https://www.linkedin.com/in/dinesh-yadav-6aa877198",
            image: linkedin
        },
        {
            name: "x",
            link: "https://x.com/tiz_3505?s=21",
            image: x
        },
        {
            name: "email",
            link: "mailto:dineshydv3505@gmail.com",
            image: email
        }
    ]

    return (
        <div className='text-black my-6 flex'>
            {links.map((item, index) => (
                <Link 
                    href={item.link}
                    target='_blank'
                    key={index}
                    onMouseEnter={() => setHoverIndex(index)}
                    onMouseLeave={() => setHoverIndex(null)}
                    className='text-black font-bold text-lg px-5 relative'
                >
                    <div className={`transition-transform duration-200 ${hoverIndex === index ? 'scale-110' : 'scale-100'}`}>
                        {item.image}
                    </div>
                </Link>
            ))}
        </div>
    )
}

export default SocialMedia

